@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal), url("NeueMontreal-Medium.ef4b1f67.otf") format("opentype");
  font-weight: 500;
  font-display: swap;
}

@font-face {
  font-family: Neue Montreal;
  src: local(Neue Montreal), url("NeueMontreal-Regular.2fa333b1.otf") format("opentype");
  font-weight: 400;
  font-display: swap;
}

body {
  color: var(--black);
  background-color: #e8e8e2;
  font-family: Neue Montreal;
  font-weight: 400;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.load {
  margin: 0 auto;
  display: flex !important;
}

.load-discount {
  padding-top: 25px;
  flex-direction: column !important;
  display: flex !important;
}

.load-total {
  margin-bottom: 8px;
  margin-right: 25px;
}

.loader {
  color: var(--green);
  width: 4px;
  aspect-ratio: 1;
  border-radius: 50%;
  margin: 0 auto;
  animation: l21 .5s linear infinite alternate;
  transform: translateX(-38px);
  box-shadow: 19px 0 0 7px, 38px 0 0 3px, 57px 0;
}

@media only screen and (min-width: 1272px) {
  .products__list.load {
    height: 1169px;
    width: 925px !important;
  }

  .popular__list.load {
    height: 546px;
    width: 275px;
  }

  .discount__list.load-discount {
    width: 283px;
    height: 484px;
  }
}

@media only screen and (min-width: 768px) {
  .products__list.load {
    height: 1536px;
    width: 704px !important;
  }

  .popular__list.load {
    height: 322px;
    width: 704px;
  }

  .popular__list.load .loader {
    width: 5px;
    height: 5px;
    display: flex;
  }

  .discount__list.load-discount {
    width: 704px;
    height: 322px;
  }
}

@media only screen and (min-width: 320px) {
  .products__list.load {
    height: 1169px;
    width: 343px;
  }

  .popular__list.load {
    height: 546px;
    width: 275px;
  }

  .discount__list.load-discount {
    width: 343px;
    height: 484px;
  }
}

.products__list .loader {
  width: 5px;
  height: 5px;
  display: flex;
}

@keyframes l21 {
  50% {
    box-shadow: 19px 0 0 3px, 38px 0 0 7px, 57px 0 0 3px;
  }

  100% {
    box-shadow: 19px 0, 38px 0 0 3px, 57px 0 0 7px;
  }
}

.container {
  max-width: 480px;
  margin: 0 auto;
  padding: 0 16px;
}

@media only screen and (min-width: 768px) {
  .container {
    max-width: 736px;
  }
}

@media only screen and (min-width: 1272px) {
  .container {
    max-width: 1272px;
  }
}

:root {
  --grey: #010101b3;
  --black: #010101;
  --light-grey: #e8e8e2;
  --cancel-grey: #01010112;
  --tagline-grey: #010101cc;
  --green: #6d8434;
