body {
  margin: 0;
  padding: 0;
}

.section-wrapper {
  padding: 0 0 80px;
}

.section-header {
  color: var(--black);
  text-transform: uppercase;
  letter-spacing: .01em;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
}

.heading-container {
  flex-direction: row;
  align-items: center;
  gap: 14px;
  margin-bottom: 17px;
  display: flex;
}

.delete-button {
  letter-spacing: -.02em;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
}

.scroll-adding {
  height: 500px;
  overflow: auto;
}

.product-list-container {
  padding-bottom: 50px;
  position: relative;
}

.button-wrapper {
  cursor: pointer;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  gap: 14px;
  margin: 0 12px 13px 0;
  display: flex;
}

.delete-button {
  background-color: #0000;
  border: none;
  margin: 0;
  padding: 0;
}

.product-list {
  flex-direction: column;
  padding-bottom: 50px;
  padding-right: 10px;
  display: flex;
  overflow: auto;
}

.product-item {
  height: 100%;
  max-width: 335px;
  flex-direction: row;
  align-items: stretch;
  gap: 20px;
  padding: 30px 0;
  display: flex;
  position: relative;
}

.product-item:not(:last-child) {
  border-bottom: 1px solid #0101011a;
}

.charactetistic-text-container {
  flex-flow: wrap;
  gap: 8px;
  display: flex;
}

.close-button {
  z-index: 1;
  cursor: pointer;
  background-color: #0000;
  border: none;
  position: absolute;
  top: 18px;
  right: 11px;
}

.product-img-wrapper {
  background-color: #fff;
  border-radius: 10px;
  padding: 18px;
}

.product-item-img {
  width: 64px;
  height: 64px;
  border-radius: 10px;
}

.product-bold {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.product-item-heading {
  padding-bottom: 10px;
}

.key-span {
  color: var(--card-fields);
  font-size: 12px;
  line-height: 1.16;
}

.product-item-text {
  font-size: 12px;
  line-height: 1.16;
}

.product-item-content {
  height: 100%;
  flex-direction: column;
  justify-content: space-between;
  gap: 36px;
  display: flex;
}

.order-total-container {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

.order-total-container .section-header {
  padding-bottom: 40px;
}

.total-container {
  width: 100%;
  box-sizing: border-box;
  background: #6d84340d;
  border-radius: 8px;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: 26px;
  padding: 18px;
  display: flex;
}

.product-price {
  font-size: 18px;
  font-weight: 500;
  line-height: 1.1;
}

.price-text {
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
}

.total-number {
  flex-direction: row;
  align-items: flex-end;
  gap: 4px;
  display: flex;
}

.order-input {
  width: 100%;
  border: 1px solid var(--card-fields);
  color: var(--black);
  box-sizing: border-box;
  background-color: #0000;
  border-radius: 30px;
  margin-bottom: 14px;
  padding: 12px 20px 10px 55px;
  font-size: 14px;
  line-height: 18px;
}

.input-wrapper {
  position: relative;
}

.order-input::placeholder {
  color: var(--black);
  font-size: 14px;
  line-height: 1.28;
}

.placeholder-span {
  color: #01010180;
  font-size: 14px;
  line-height: 1.28;
  position: absolute;
  top: 12.5px;
  left: 20px;
}

.order-submit {
  background: var(--green);
  color: var(--light-grey);
  width: 100%;
  cursor: pointer;
  border: none;
  border-radius: 30px;
  padding: 12px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.28;
  transition: background-color .5s;
}

.order-submit:hover, .order-submit:focus {
  background-color: #586f1f;
}

.cart-icon-wrapper {
  background: #6d8434;
  border-radius: 30px;
  padding: 10px 10px 7px;
}

.cart-icon {
  fill: var(--light-grey);
}

.warning-container {
  text-align: center;
  width: 279px;
  margin: 100px auto 40px;
}

.warning-image {
  margin: 0 auto 28px;
  display: block;
}

.warning-descr {
  margin-bottom: 14px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.11;
}

.warning-descr .color-text {
  color: var(--green);
}

.warning-text {
  color: #010101b3;
  font-size: 14px;
  line-height: 1.28;
}

